/* 
    scss는 기본적으로 css와 같습니다. 그러나 밑에 서술한
    기능들을 추가로 이용할 수 있어 사용합니다. 필요한 경우
    사용하시면 되겠습니다.


    nesting 예시
    div.container{
    h4{
        color
    }

    p{
        color
    }

    @extend => 복붙하지 말고

    함수는
    @mixin function{
    }
    사용은
    @include function()

    변수 저장은
    $main : #ffff;
*/
