//PC 시작
.Land{
    height: 100vh;
    min-height: 1150px;
    margin: 0;
    background: radial-gradient(50% 50% at 50% 50%, #cfecd4, rgba(255, 255, 255, 0)), 
                radial-gradient(50% 50% at 50% 50%, #fffada, rgba(255, 255, 255, 0)), 
                #ced7e4;
    background-size: 200% 200%; /* 배경 크기 초기 설정 */
    background-position: 0% 0%; /* 배경 위치 초기 설정 */
    animation: gradientAnimation 10s ease infinite; /* 애니메이션 적용 */
}

@keyframes gradientAnimation {
    0% {
        background-position: 0% 0%;
    }
    50% {
        background-position: 100% 100%;
    }
    100% {
        background-position: 0% 0%;
    }
}

