// 2번 페이지 시작
.Activities {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 95vh;
    min-height: 1150px;
    position: relative;
    padding: 50px 0;

    // 배경 원 추가
    .activities-background {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -1;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
    
        // 왼쪽 반원
        &::before {
            content: "";
            position: absolute;
            width: 50vw; 
            height: 50vw;
            top: 15%; 
            left: -15%;
            background: radial-gradient(circle, #f0f4f8 30%, rgba(255, 255, 255, 0) 80%);
            border-radius: 50%;
        }
    
        // 가운데 원
        &::after {
            content: "";
            position: absolute;
            width: 50vw;
            height: 50vw;
            top: 15%;
            left: 50%;
            transform: translateX(-50%);
            background: radial-gradient(circle, #f0f4f8 35%, rgba(255, 255, 255, 0) 85%);
            border-radius: 50%;
        }
    
        // 오른쪽 반원 
        .right-circle {
            content: "";
            position: absolute;
            width: 50vw;
            height: 50vw;
            top: 15%;
            right: -15%;
            background: radial-gradient(circle, #f0f4f8 30%, rgba(255, 255, 255, 0) 80%);
            border-radius: 50%;
        }
    }            

    // 헤더 스타일링
    .activities-header {
        position: absolute;
        top: 5%; 
        left: 50%;
        transform: translateX(-50%);
        

        .activities-title {
            font-family: "locus_sangsang";
            font-size: 32px;
            color: #486284;
            font-weight: bold;
            margin-bottom: 100px;
        }

        .activities-description {
            font-size: 15px;
            color: #486284;
            font-family: "Pretendard Variable";
            line-height: 1.6;
            margin-top: 20px
        }
    }

    // 활동 리스트
    .activity-list {
        display: flex;
        justify-content: center;
        gap: 50px;
        width: 100%;
        max-width: 1000px;
        margin-top: 60px;

        .activity {
            background: edf5ff;
            padding: 40px;
            border-radius: 12px;
            width: 280px;
            box-shadow: 0px 4px 15px rgba(0, 0, 0, 0);
            text-align: center;

            .activity-icon {
                width: 50px; 
                height: auto;
                margin-bottom: 20px;
            }

            h3 {
                font-size: 20px;
                font-weight: bold;
                color: #1a202c;
                font-family: "Pretendard Variable";
                margin-bottom: 15px;
            }

            p {
                font-size: 12px;
                color: #486284;
                font-family: "Pretendard Variable";
                line-height: 1.6;
                max-width: 350px;
                margin: 0 auto;
            }
        }
    }
}

