/* 1) 전체 컨테이너에 그라디언트 배경 */
.qna-container {
  width: 100%;
  height: 100vh;
  min-height: 1400px;
  /* 예시: 부드러운 파스텔 그라디언트 */
  background: 
    radial-gradient(50% 50% at 50% 50%, #cfecd4, transparent),
    radial-gradient(50% 50% at 50% 50%, #fffada, transparent),
    #ced7e4;
  background-size: 200% 200%;
  background-position: 0% 0%;
  animation: gradientAnimation 10s ease infinite;

  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 80px 0px 80px 0px;
  box-sizing: border-box;
}

/* 그라디언트 애니메이션 */
@keyframes gradientAnimation {
  0% {
    background-position: 0% 0%;
  }
  50% {
    background-position: 100% 100%;
  }
  100% {
    background-position: 0% 0%;
  }
}

/* 2) 제목/부제목 중앙 정렬 */
