/* 질문(.Q) 말풍선 - 왼쪽 */
.PC-Q {
  position: relative;
  align-self: flex-start; /* 왼쪽 정렬 */
  max-width: 80%;
  padding: 15px;
  background-color: #fff; /* 원하는 색상 */
  border-radius: 20px;    /* 말풍선 둥근 모서리 */
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);

  &::after {
    content: "";
    position: absolute;
    left: -15px; /* 꼬리가 왼쪽 바깥으로 */
    top: 30px;   /* 말풍선 높이에 따라 조정 */
    width: 0;
    height: 0;
    border: 10px solid transparent;
    border-right-color: #fff; /* 질문 말풍선 배경색과 동일 */
    transform: rotate(-10deg);
  }
}

.PC-Q-text {
  font-size: 16px;
  letter-spacing: -0.03em;
  line-height: 150%;
  font-family: 'Pretendard Variable';
  color: #1a202c;
}

/* 답변(.A) 말풍선 - 오른쪽 */
.PC-A {
  position: relative;
  align-self: flex-end; /* 오른쪽 정렬 */
  max-width: 60%;
  padding: 16px;
  background-color: #90A3BF; /* 원하는 색상 */
  border-radius: 20px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);

  &::after {
    content: "";
    position: absolute;
    right: -20px; /* 꼬리가 오른쪽 바깥으로 */
    top: 30px;
    width: 0;
    height: 0;
    border: 10px solid transparent;
    border-left-color: #90A3BF; /* 답변 말풍선 배경색과 동일 */
  }
}

.PC-A-text {
  font-size: 16px;
  line-height: 1.4;
  color: #fff;
}


