    display: flex;
    flex-direction: row;
    gap: 5px;
    justify-content: flex-end;
}
.b_size{
    height: 20px;
    width: 20px;
}

//Alumni 시작
.Alumni{
    padding: 0px 24px 30px 24px;
    display: flex;
    flex-direction: column;
    gap: 30px;

}

.Alumni-head{
    font-size: 15px;
    letter-spacing: -0.03em;
    line-height: 150%;
    font-weight: 500;
    font-family: 'Pretendard Variable';
    color: #486284;
    text-align: center;
}
.Alumni-sort{
    height: 43px;
    font-size: 12px;
    letter-spacing: -0.02em;
    line-height: 150%;
    font-family: 'Pretendard Variable';
    color: #1a202c;
    text-align: center;
    display: grid;
    grid-template-columns: 100px 125px 120px;
    align-items: center;
    justify-content: center;

}

.Alumni-info-head{
    height: 43px;
    font-size: 12px;
    letter-spacing: -0.02em;
    line-height: 150%;
    font-weight: 300;
    font-family: 'Pretendard Variable';
    color: #1a202c;
    text-align: center;
    display: grid;
    grid-template-columns: 100px 130px 120px;
    align-items: center;
    justify-content: center;
}

.Alumni-info{
    height: 43px;
    font-size: 12px;
    letter-spacing: -0.02em;
    line-height: 150%;
    font-weight: 300;
    font-family: 'Pretendard Variable';
    color: #486284;
    text-align: center;
    display: grid;
    grid-template-columns: 100px 130px 120px;
    align-items: center;
    justify-content: center;
    border-top: 1px solid #90a3bf;
}